
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import FileItem from 'c%/FileItem.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'


export default{
mixins: [ common, globals ],
components: {
FileItem,
Timeline,
TimelineItem
},
data () {
return {
data: []
}
},
methods: {
detail (item) {
this.$router.push({path:'/fp/'+item.file_package.short_id})
}
},
computed: {
data_uri () {
let uri = util.sprintf('service_push_tasks?with=file_package.file_package_file&bid=-1&sid=%s',this.sid$)

return uri
},
dict_type () {
if(this.cid>0){
return 'class_service_type'
}else{
return 'student_service_type'
}
}
}
}



